h1 {
  text-align: center;
  background-color: grey;
  color: white;
  border: 2px solid black;
  border-radius: 21px;
  font-size: 40px;
  font-family: cursive;
}
.list-box {
  /* display: flex;
  align-items: center;
  justify-content: center; */
  padding: 12px;
  background-color: darkred;
}
ul {
  list-style: square;
  color: white;
}
li {
  padding: 20px;
  width: 100%;
  font-size: 25px;
}
button {
  float: right;
  padding: 6px;
  margin-right: 48px;
  cursor: pointer;
  background-color: white;
  font-weight: bold;
  font-size: 15px;
}

button:hover {
  color: white;
  background-color: black;
}
p {
  font-size: 14px;
  font-family: monospace;
}
